<template>
  <div id="myWorkExperience" class="swiper-slide">
    <p class="title"></p>
    <TimeAxis>
      <TimeAxisItem
        date="20181"
        title=""
        content="20177"
      />
      <TimeAxisItem
        date="20186"
        title=""
        content="JavaweblinuxArduinomySQl2017"
      />
      <TimeAxisItem
        date="20188"
        title=""
        content="gitElement UIvuereact"
      />
    </TimeAxis>
  </div>
</template>
<script>
import TimeAxis from '@/components/TimeAxis'
import TimeAxisItem from '@/components/TimeAxis/timeAxisItem'
export default {
  name: 'myWorkExperience',
  components: {
    TimeAxis,
    TimeAxisItem
  }
}
</script>

<style lang="scss">
#myWorkExperience {
  position: relative;
  background: gray;
  width: 100%;
  height: 100%;
  .title {
    text-align: center;
  }
}
</style>



